<template>
  <div>
    <h1>Liste des Villes</h1>
    <ul>
      <li v-for="ville in villes" :key="ville.id">{{ ville.nom }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      villes: [],
    };
  },
  created() {
    this.fetchVilles();
  },
  methods: {
    async fetchVilles() {
      try {
        const response = await fetch('/api/ville-list/');
        this.villes = await response.json();
      } catch (error) {
        console.error('Erreur lors de la récupération des villes:', error);
      }
    },
  },
};
</script>