<template>
    <div>
      <h1>Détails de la Ville</h1>
      <p>Nom: {{ ville.nom }}</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        ville: null,
      };
    },
    created() {
      this.fetchVilleDetails();
    },
    methods: {
      async fetchVilleDetails() {
        try {
          const response = await fetch(`/api/ville-detail/${this.$route.params.id}/`);
          this.ville = await response.json();
        } catch (error) {
          console.error('Erreur lors de la récupération des détails de la ville:', error);
        }
      },
    },
  };
  </script>